@charset "utf-8";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
}

.main ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 10px;
}

.main ul li {
  width: 49%;
}

.main ul li img {
  width: 100%;
}

.main ul li .title {
  font-weight: bold;
  height: 40px;
}

.main ul li .price {
  text-align: right;
  font-weight: bold;
}

.main ul li .deleteBox {
  text-align: right;
  padding: 5px 0 10px;
}

.main ul li .delete {
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  color: #ffffff;
  background-color: #ff4646;
}

.inputData {
  padding: 20px 0;
  margin-bottom: 30px;
  background-color: #ffd972;
}

.inputData p {
  display: flex;
  justify-content: center;
  margin: 5px 10px;
}

.inputData p label {
  width: 60px;
}

.inputData input[type=button] {
  border: none;
  border-radius: 5px;
  height: 34px;
  margin-top: 10px;
  color: #ffffff;
  background-color: #52A46E;
  cursor: pointer;
}

.inputData input {
  width: calc(100% - 60px);
  padding: 5px;
}
